html,body {
    background-color: #FFf;
}
body {
    font: 14px/1.5 Tahoma, Helvetica, Arial, \5b8b\4f53;
    color: #000;
}
a {
    color: #0099CC;
    text-decoration: none;
}
a:hover {
    color: #0099CC;
    text-decoration: none;
}
.left {
    float: left;
}
.right {
    float: right;
}
.clear {
    clear: both;
}

/* ------------ common ---------------------------- */

/* ------------ header ---------------- */
header {
    text-align: left;
    padding: 0;
    margin: 15px auto;
}
#logo {
    height: 39px;
}
#shortcuts {
    margin: 15px 0 0 15px;
}
#nav {
    float: center;
    margin-top: 10px;
}
#nav li {
    margin: 0 7px;
}
#nav li a {
    font-size: 22px;
    color: #0099CC;
}
#nav li.active a , #nav li.active a:hover {
    background-color: #0099CC;
    color: #FFF;
}
#nav li a:hover {
    background-color: #79C6DF;
    color: #FFF;
}

/* ------------ index ---------------------------- */
#index-box {
    background-color: #fff;
    width: 940px;
    clear: both;
    -webkit-box-shadow: 0 0 4px 2px #ddd;
            box-shadow: 0 0 4px 2px #ddd;
    border: 1px solid #ddd;
}
#index-box .float-box {
    float: left;
    margin: 20px 0 20px 20px;
    display: inline;
}
#index-box #content-nav {
    margin: 0;
    width: 200px;
}
#index-box #content-nav li a {
    background-color: #E5ECDC;
}
#index-slider {
    width: 940px;
    height: 200px;
    overflow: hidden;
    position: relative;
}
#index-slider ul {
    list-style: none;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
}
#index-slider li {
    width: 940px;
    height: 200px;
    float: left;
}
#index-slider #pagination {
    top: 160px;
    left: 800px;
}
#index-slider ul#pagination li {
    width: 16px;
    height: 16px;
    border-radius: 1em;
    background-color: #FFF;
    -webkit-box-shadow: 0 0 1px 2px #CCC;
            box-shadow: 0 0 1px 2px #CCC;
    margin-right: 18px;
    margin-top: 4px;
}
#index-slider ul#pagination li.active {
    width: 24px;
    height: 24px;
    margin-top: 0;
}
#index-slider ul#pagination li:hover {
    background-color: #DDD;
    cursor: pointer;
}
#index-report {
    width: 680px;
    height: 70px;
    margin-top: 20px;
    overflow: hidden;
    position: relative;
    background-color: #E5ECDC;
}
#index-report ul {
    list-style: none;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
}
#index-report li {
    width: 680px;
    height: 70px;
    text-align: center;
    float: left;
    font-size: 32px;
    color: #666;
    font-family: å¾®è½¯é›…é»‘,é»‘ä½“;
    line-height: 70px;
}
#index-report-pagination {
    list-style: none;
    margin-left: 35px;
    text-align: center;
}
#index-report-pagination li {
    height: 65px;
    float: left;
    margin-right: 45px;
    cursor: pointer;
}
#index-report-pagination li img {
    margin-top: 30px;
}
#index-report-pagination li.active {
}
#index-button {
    margin: 40px 0 30px;
    text-align: center;
}
#index-button a {
    margin: 0 50px;
}

/* ------------ content ---------------------------- */
#tab {
    margin-top: 30px;
}
#tab li a {
    margin-right: 10px;
    border-bottom-color: #ddd;
    color: #666;
}
#tab li a:hover {
    background-color: #F2F2F2;
    color: #333;
    border-color: #ddd;
    border-bottom-color: transparent;
}
#tab li.active a {
    background-color: #F2F2F2;
    color: #666;
    border-bottom-color: transparent;
}
#content-nav {
    list-style: none;
    margin: 0;
    margin-top: 20px;
}
#content-nav li a {
    color: #666;
    padding: 11px 0 11px 60px;
    display: block;
    margin-bottom: 1px;
    font-size: 22px;
}
